:root {
    --accent: #ef946c;
    --purpleBorder: #2f2963;
    --darkGreen: #439a86;
    --lightGreen: #9ec8c2;
    --purple: #454372;
    --grey: #f7f7ff;
    --black: rgba(0, 0, 0, 0.79);
}

body {
    padding: 0;
    margin: 0;
}

h1 {
    font-weight: bold;
    margin: 0;
    font-size: 7vh;
    font-family: Raleway, serif;
    color: rgba(0, 0, 0, 0.79);
    color: var(--black);
}

h2 {
    font-weight: normal;
    margin: 0;
    font-size: 4vh;
    font-family: Raleway, serif;
    color: rgba(0, 0, 0, 0.79);
    color: var(--black);
}

a {
    font-weight: normal;
    margin: 0;
    font-family: Raleway, serif;
    color: rgba(0, 0, 0, 0.79);
    color: var(--black);
    text-decoration: none;
}

p {
    margin: 0;
    font-family: Raleway, serif;
    color: rgba(0, 0, 0, 0.79);
    color: var(--black);
}

.heroContainer {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    background-color: var(--darkGreen);
    padding: 5vw 0;
}

.text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4vw 2vw;
}

.image {
    max-width: 1000px;
    min-width: 100px;
}
.navBar {
    display: flex;
    justify-content: space-between;
    padding: 5px 15px;
    border-bottom: var(--black);
    border-top: var(--black);
    border-style: solid;
    border-width: 2px;
    flex-wrap: wrap;
    align-items: baseline;
    background-color: white !important;
    color: var(--black) !important;
}

.navBarItems {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: flex-end;

}

button {
    font-size: large!important;
    color: var(--black) !important;
}

a {
    font-size: 3vh;
}

.syllabusContainer {
    display: flex;
    flex-wrap: wrap;
    padding: 5vw 0 0 0;
    background-color: var(--grey);
    flex-direction: column;
    align-content: center;
}

.syllabusTimelineContainer {
/*    display: flex;*/
    padding: 2vw 0 0 0;
    background-color: var(--grey);
/*    flex-direction: row;*/
}

